   /* Add styles for seat layout */
.seat-container {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: center;
}

.seat-wrapper {
  width: 40px; /* Set a fixed width for each seat */
  height: 40px; /* Set a fixed height for each seat */
  margin: 5px;
}

.seat {
  width: 100%; /* Ensure the button fills the wrapper */
  height: 100%;
  font-size: 12px;
  text-align: center;
  padding: 0;
  border-radius: 5px;
  border: none;
}

.seat-available {
  background-color: #28a745;
}

.seat-selected {
  background-color: #007bff;
  color: white;
}

.seat-booked {
  background-color: #dc3545;
  color: white;
}

.seat-reserved {
  background-color: #ffc107;
  color: black;
}

@media (max-width: 768px) {
  .seat-wrapper {
    width: 35px;
    height: 35px;
  }

  .seat {
    font-size: 10px;
  }
}

@media (max-width: 576px) {
  .seat-wrapper {
    width: 30px;
    height: 30px;
  }

  .seat {
    font-size: 8px;
  }
}
